.burger-menu {
  position: relative;
  width: 50px;
  height: 50px;
  cursor: pointer;
}
.burger-menu.open .burger {
  background: rgba(111,111,111,0);
}
.burger-menu.open .burger:after {
  transform: rotate(-45deg);
  bottom: 0px;
}
.burger-menu.open .burger:before {
  transform: rotate(45deg);
  top: 0px;
}
.burger {
  position: absolute;
  background: #6f6f6f;
  width: 100%;
  height: 10px;
  top: 50%;
  right: 0px;
  margin-top: -5px;
  opacity: 1;
  transition: 0.4s ease-in-out;
}
.burger:before {
  position: absolute;
  background: #6f6f6f;
  width: 50px;
  height: 10px;
  top: 20px;
  content: "";
  display: block;
  transition: 0.4s ease-in-out;
}
.burger:after {
  position: absolute;
  background: #6f6f6f;
  width: 50px;
  height: 10px;
  bottom: 20px;
  content: "";
  display: block;
  transition: 0.4s ease-in-out;
}
